	created() {
		this.get_items();
	},
	methods: {
		get_items() {
			if (this.category !== 'All') {
				this.filters['hub_category'] = this.category;
			}
			hub.call('get_items', {
				keyword: this.search_value,
				filters: this.filters
			})
			.then((items) => {
				this.items = items;
			})
		},

		set_route_and_get_items() {
			frappe.set_route('marketplace', 'search', this.category, this.search_value);
			this.get_items();
		},

